body {
  margin: 0;
  padding: 0; }

.container {
  width: 200px;
  height: 100px;
  padding-top: 100px;
  margin: 0 auto; }

.ball {
  width: 90px;
  height: 10px;
  margin: 10px auto;
  border-radius: 50px; }

.ball:nth-child(1) {
  background: #ff005d;
  -webkit-animation: right 1s infinite ease-in-out;
  animation: right 1s infinite ease-in-out; }

.ball:nth-child(2) {
  background: #35ff99;
  -webkit-animation: left 1.1s infinite ease-in-out;
  animation: left 1.1s infinite ease-in-out; }

.ball:nth-child(3) {
  background: #008597;
  -webkit-animation: right 1.05s infinite ease-in-out;
  animation: right 1.05s infinite ease-in-out; }

.ball:nth-child(4) {
  background: #ffcc00;
  -webkit-animation: left 1.15s infinite ease-in-out;
  animation: left 1.15s infinite ease-in-out; }

.ball:nth-child(5) {
  background: #2d3443;
  -webkit-animation: right 1.1s infinite ease-in-out;
  animation: right 1.1s infinite ease-in-out; }

.ball:nth-child(6) {
  background: #ff7c35;
  -webkit-animation: left 1.05s infinite ease-in-out;
  animation: left 1.05s infinite ease-in-out; }

.ball:nth-child(7) {
  background: #4d407c;
  -webkit-animation: right 1s infinite ease-in-out;
  animation: right 1s infinite ease-in-out; }

@-webkit-keyframes right {
  0% {
    -webkit-transform: translate(-15px); }
  50% {
    -webkit-transform: translate(15px); }
  100% {
    -webkit-transform: translate(-15px); } }

@-webkit-keyframes left {
  0% {
    -webkit-transform: translate(15px); }
  50% {
    -webkit-transform: translate(-15px); }
  100% {
    -webkit-transform: translate(15px); } }

@keyframes right {
  0% {
    -webkit-transform: translate(-15px);
            transform: translate(-15px); }
  50% {
    -webkit-transform: translate(15px);
            transform: translate(15px); }
  100% {
    -webkit-transform: translate(-15px);
            transform: translate(-15px); } }

@keyframes left {
  0% {
    -webkit-transform: translate(15px);
            transform: translate(15px); }
  50% {
    -webkit-transform: translate(-15px);
            transform: translate(-15px); }
  100% {
    -webkit-transform: translate(15px);
            transform: translate(15px); } }

.cubes .cube {
  position: absolute;
  height: 100px;
  width: 100px;
  margin: 0;
  -webkit-animation: cube-fade-in 2s cubic-bezier(0.165, 0.84, 0.44, 1);
          animation: cube-fade-in 2s cubic-bezier(0.165, 0.84, 0.44, 1);
  will-change: transform;
  z-index: -1; }

@-webkit-keyframes cube-fade-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
            transform: scale(0.5); } }

@keyframes cube-fade-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
            transform: scale(0.5); } }
  .cubes .cube * {
    position: absolute;
    height: 100%;
    width: 100%; }
  .cubes .cube .shadow {
    background: #07427a;
    top: 40%; }
  .cubes .cube .sides {
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-perspective: 600px;
            perspective: 600px; }
    .cubes .cube .sides div {
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
      will-change: transform; }
    .cubes .cube .sides .front {
      -webkit-transform: rotateY(0deg) translateZ(50px);
              transform: rotateY(0deg) translateZ(50px); }
    .cubes .cube .sides .back {
      -webkit-transform: rotateY(-180deg) translateZ(50px);
              transform: rotateY(-180deg) translateZ(50px); }
    .cubes .cube .sides .left {
      -webkit-transform: rotateY(-90deg) translateZ(50px);
              transform: rotateY(-90deg) translateZ(50px); }
    .cubes .cube .sides .right {
      -webkit-transform: rotateY(90deg) translateZ(50px);
              transform: rotateY(90deg) translateZ(50px); }
    .cubes .cube .sides .top {
      -webkit-transform: rotateX(90deg) translateZ(50px);
              transform: rotateX(90deg) translateZ(50px); }
    .cubes .cube .sides .bottom {
      -webkit-transform: rotateX(-90deg) translateZ(50px);
              transform: rotateX(-90deg) translateZ(50px); }

